import useQueriedData from "../hooks/useQueriedData";
import { useParams } from "react-router-dom";

import { Typography } from "@mui/material";

const User = () => {
    const blogs = useQueriedData("blogs");
    const userId = useParams().id;

    const userBlogs = blogs.filter((blog) => blog.user.id === userId);
    const user = userBlogs[0].user;
    return (
        <div>
            <Typography
                variant="h4"
                component="h2"
            >
                {user.name}
            </Typography>
            <Typography
                variant="h5"
                component="h3"
            >
                added blogs
            </Typography>
            <ul>
                {userBlogs.map((userBlog) => {
                    return (
                        <Typography
                            key={userBlog.id}
                            component="li"
                        >
                            {userBlog.title}
                        </Typography>
                    );
                })}
            </ul>
        </div>
    );
};

export default User;
