import useQueriedData from "../hooks/useQueriedData";

import { Link } from "react-router-dom";

import {
    Typography,
    Table,
    TableRow,
    TableCell,
    TableBody,
    TableHead,
} from "@mui/material";

import countBy from "lodash.countby";

const Users = () => {
    const blogs = useQueriedData("blogs");

    const userBlogs = Object.entries(
        countBy(blogs, function (obj) {
            return JSON.stringify({
                name: obj.user.name,
                id: obj.user.id,
            });
        })
    );

    return (
        <div>
            <Typography
                variant="h4"
                component="h2"
                gutterBottom
            >
                Users
            </Typography>
            <Table size="small">
                <TableHead>
                    <TableRow
                        sx={{
                            "&>th": {
                                fontWeight: 700,
                                fontSize: "15px",
                            },
                        }}
                    >
                        <TableCell>Users</TableCell>
                        <TableCell>Number of Blogs Created</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {userBlogs.map(([user, numberOfBlogs]) => {
                        const { name, id } = JSON.parse(user);
                        return (
                            <TableRow key={id}>
                                <TableCell>
                                    <Link to={`/users/${id}`}>{name}</Link>
                                </TableCell>
                                <TableCell>{numberOfBlogs}</TableCell>
                            </TableRow>
                        );
                    })}
                </TableBody>
            </Table>
        </div>
    );
};

export default Users;
