import { useState } from "react";
import { useNotificationDispatch } from "../context/NotificationContext";
import { useUserDispatch } from "../context/UserContext";

import { TextField, Button, Stack } from "@mui/material";
import LoginIcon from "@mui/icons-material/Login";

import loginService from "../services/login";
import blogService from "../services/blogs";

const LoginForm = () => {
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");

    const displayMessage = useNotificationDispatch();
    const setUser = useUserDispatch();

    const handleLogin = async (event) => {
        event.preventDefault();
        try {
            const presentUser = await loginService.login({
                username,
                password,
            });
            window.localStorage.setItem(
                "loggedInBlogappUser",
                JSON.stringify(presentUser)
            );
            setUser({ type: "user/set", payload: presentUser });
            setUsername("");
            setPassword("");
            blogService.setToken(presentUser.token);
        } catch (exception) {
            displayMessage({
                type: "error",
                text: "Invalid username or password",
            });
        }
    };
    return (
        <>
            <form onSubmit={handleLogin}>
                <Stack
                    spacing={1}
                    display="block"
                >
                    <div>
                        <TextField
                            type="input"
                            name="username"
                            id="username"
                            value={username}
                            onChange={({ target }) => setUsername(target.value)}
                            required
                            variant="outlined"
                            label="username"
                            size="small"
                        />
                    </div>
                    <div>
                        <TextField
                            type="password"
                            name="password"
                            id="password"
                            value={password}
                            onChange={({ target }) => setPassword(target.value)}
                            required
                            variant="outlined"
                            label="password"
                            size="small"
                        />
                    </div>
                    <Button
                        type="submit"
                        variant="contained"
                        color="success"
                        disableElevation
                        endIcon={<LoginIcon />}
                    >
                        Login
                    </Button>
                </Stack>
            </form>
        </>
    );
};

export default LoginForm;
