import { useEffect, useRef } from "react";
import { useQuery } from "@tanstack/react-query";
import { useUserDispatch, useUserValue } from "./context/UserContext";

import BlogList from "./components/BlogList";
import LoginForm from "./components/LoginForm";
import BlogForm from "./components/BlogForm";
import Notification from "./components/Notification";
import Toggleable from "./components/Toggleable";
import Header from "./components/Header";
import Users from "./components/Users";
import User from "./components/User";
import Blog from "./components/Blog";
import NavigationPane from "./components/NavigationPane";

import { Box, Stack, Container, CssBaseline } from "@mui/material";
import CreateIcon from "@mui/icons-material/Create";

import { Routes, Route, Navigate } from "react-router-dom";

import blogService from "./services/blogs";

const App = () => {
    const blogForm = useRef(null);

    const user = useUserValue();
    const setUser = useUserDispatch();

    const blogsQuery = useQuery(["blogs"], blogService.getAll, {
        refetchOnWindowFocus: false,
        enabled: Boolean(user),
        initialData: [],
    });

    const blogs = blogsQuery.data;

    useEffect(() => {
        const loggedUserJson = window.localStorage.getItem(
            "loggedInBlogappUser"
        );
        if (loggedUserJson) {
            const user = JSON.parse(loggedUserJson);
            setUser({
                type: "user/set",
                payload: user,
            });
            blogService.setToken(user.token);
        }
    }, []);

    if (blogsQuery.isFetching) {
        return <div>loading blogs...</div>;
    }

    return (
        <>
            <CssBaseline />
            <Box
                sx={{
                    height: "100vh",
                    paddingTop: !user && "50px",
                }}
            >
                <div>
                    {user && <NavigationPane />}
                    <Container>
                        <Header />
                        <Notification />
                        <Routes>
                            <Route
                                path="/"
                                element={
                                    <div>
                                        {" "}
                                        {user ? (
                                            <Stack
                                                display="block"
                                                spacing={2}
                                            >
                                                <Toggleable
                                                    buttonLabel={"create blog"}
                                                    ref={blogForm}
                                                    icon={<CreateIcon />}
                                                >
                                                    <BlogForm
                                                        blogForm={blogForm}
                                                    />
                                                </Toggleable>
                                                <BlogList />
                                            </Stack>
                                        ) : (
                                            <LoginForm />
                                        )}
                                    </div>
                                }
                            />
                            <Route
                                path="/users"
                                element={
                                    user ? (
                                        <Users blogs={blogs} />
                                    ) : (
                                        <Navigate to="/" />
                                    )
                                }
                            />
                            <Route
                                path="/users/:id"
                                element={user ? <User /> : <Navigate to="/" />}
                            />
                            <Route
                                path="/blogs/:id"
                                element={user ? <Blog /> : <Navigate to="/" />}
                            />
                            <Route
                                path="/*"
                                element={<Navigate to="/" />}
                            />
                        </Routes>
                    </Container>
                </div>
            </Box>
        </>
    );
};

export default App;
