import { useUserValue } from "../context/UserContext";
import { Typography } from "@mui/material";

const Header = () => {
    const user = useUserValue();

    return (
        <Typography
            variant="h3"
            component="h1"
            gutterBottom
        >
            {user ? "Blog app" : "Log into application"}
        </Typography>
    );
};

export default Header;
