import { useState } from "react";
import { useNotificationDispatch } from "../context/NotificationContext";
import { useMutation, useQueryClient } from "@tanstack/react-query";

import blogService from "../services/blogs";

import { Button, TextField, Typography, Stack } from "@mui/material";
import AddIcon from "@mui/icons-material/Add";

const BlogForm = ({ blogForm }) => {
    const [title, setTitle] = useState("");
    const [author, setAuthor] = useState("");
    const [url, setUrl] = useState("");

    const displayMessage = useNotificationDispatch();

    const queryClient = useQueryClient();
    const addBlogMutation = useMutation(blogService.postBlog, {
        onSuccess: (savedBlog) => {
            const blogs = queryClient.getQueryData(["blogs"]);
            queryClient.setQueryData(["blogs"], blogs.concat(savedBlog));
            displayMessage({
                type: "success",
                text: `"${savedBlog.title}" by ${savedBlog.author} added`,
            });
        },
        onError: (error, blog) => {
            displayMessage({
                text: `unable to add "${blog.title}"`,
                type: "error",
            });
        },
    });

    const addBlog = async (event) => {
        event.preventDefault();
        const blog = {
            title,
            author,
            url,
        };

        try {
            addBlogMutation.mutate(blog);
            blogForm.current.toggleVisibility();
            setTitle("");
            setAuthor("");
            setUrl("");
        } catch (error) {
            console.log(error);
        }
    };

    return (
        <>
            <Typography
                variant="h5"
                component="h2"
                gutterBottom
            >
                Create New
            </Typography>
            <form onSubmit={addBlog}>
                <Stack
                    display="block"
                    spacing={1}
                >
                    <Input
                        text="title"
                        value={title}
                        onChange={setTitle}
                        id="title"
                    />
                    <Input
                        text="author"
                        value={author}
                        onChange={setAuthor}
                        id="author"
                    />
                    <Input
                        text="url"
                        value={url}
                        onChange={setUrl}
                        id="url"
                    />
                    <Button
                        type="submit"
                        variant="contained"
                        color="success"
                        endIcon={<AddIcon />}
                    >
                        Create
                    </Button>
                </Stack>
            </form>
        </>
    );
};

const Input = ({ text, value, onChange }) => {
    return (
        <div>
            <TextField
                type="text"
                name={text}
                id={text}
                value={value}
                onChange={({ target }) => onChange(target.value)}
                required
                label={text}
                variant="filled"
                size="small"
            ></TextField>
        </div>
    );
};

export default BlogForm;
