import { useNotificationDispatch } from "../context/NotificationContext";
import { useMutation, useQueryClient } from "@tanstack/react-query";
import useQueriedData from "../hooks/useQueriedData";
import { useParams } from "react-router-dom";
import { useUserValue } from "../context/UserContext";
import { useNavigate } from "react-router-dom";

import blogService from "../services/blogs";

import { Typography, Link, Button, Stack } from "@mui/material";
import DeleteIcon from "@mui/icons-material/Delete";
import FavoriteIcon from "@mui/icons-material/Favorite";

import Comments from "./Comments";

const Blog = () => {
    const blogId = useParams().id;
    const blog = useQueriedData("blogs").find((b) => b.id === blogId);

    const user = useUserValue();

    const navigate = useNavigate();

    const displayMessage = useNotificationDispatch();
    const queryClient = useQueryClient();

    const updateBlogMutation = useMutation(blogService.updateBlog, {
        onSuccess: (updatedBlog) => {
            const blogs = queryClient.getQueryData(["blogs"]);
            queryClient.setQueryData(
                ["blogs"],
                blogs.map((b) => (b.id !== updatedBlog.id ? b : updatedBlog))
            );
        },
        onError: (error, blog) => {
            displayMessage({
                text: `Unable to update ${blog.title}`,
                type: "error",
            });
        },
    });

    const deleteBlogMutation = useMutation(blogService.deleteBlog, {
        onSuccess: (serviceData, deletedBlog) => {
            const blogs = queryClient.getQueryData(["blogs"]);
            queryClient.setQueryData(
                ["blogs"],
                blogs.filter((b) => b.id !== deletedBlog.id)
            );
            displayMessage({
                text: `Deleted "${deletedBlog.title}"`,
                type: "success",
            });
        },
        onError: (error, blog) => {
            displayMessage({
                text: `Unable to delete "${blog.title}"`,
                type: "error",
            });
        },
    });

    const increaseLikes = () => {
        const newBlog = {
            ...blog,
            likes: blog.likes + 1,
        };
        try {
            updateBlogMutation.mutate(newBlog);
        } catch (error) {
            console.log(error);
        }
    };

    const deleteBlog = () => {
        const checkDelete = window.confirm(
            `Remove blog "${blog.title}" by ${blog.author}?`
        );
        if (checkDelete) {
            try {
                deleteBlogMutation.mutate(blog);
                navigate("/");
            } catch (error) {
                console.log(error);
            }
        }
    };

    return (
        <div>
            <Stack
                spacing="5px"
                display="block"
            >
                <Typography
                    variant="h4"
                    component="h2"
                >
                    {blog.title} - {blog.author}
                </Typography>
                <Link
                    href={`https://${blog.url}`}
                    target="_blank"
                    rel="noreferrer noopener"
                    variant="h5"
                >
                    {blog.url}
                </Link>
                <Typography>
                    <span style={{ paddingRight: "10px" }}>
                        Likes: {blog.likes}{" "}
                    </span>
                    <Button
                        onClick={increaseLikes}
                        variant="outlined"
                        size="small"
                        sx={{
                            "&:hover": {
                                backgroundColor: "primary.main",
                                color: "white",
                            },
                        }}
                        disableRipple
                        endIcon={<FavoriteIcon />}
                    >
                        like
                    </Button>
                </Typography>
                <Typography>added by {blog.user.name}</Typography>
                {user.username === blog.user.username && (
                    <Button
                        onClick={deleteBlog}
                        className="remove-button"
                        variant="contained"
                        color="error"
                        disableElevation
                        endIcon={<DeleteIcon />}
                    >
                        Remove
                    </Button>
                )}
            </Stack>
            <Comments blog={blog} />
        </div>
    );
};

export default Blog;
