import { useUserDispatch, useUserValue } from "../context/UserContext";
import { useNavigate } from "react-router-dom";

import blogService from "../services/blogs";

import { Link } from "react-router-dom";

import { AppBar, Toolbar, Button, Typography } from "@mui/material";

import LogoutIcon from "@mui/icons-material/Logout";

const NavigationPane = () => {
    const user = useUserValue();
    const setUser = useUserDispatch();
    const navigate = useNavigate();

    const logOut = () => {
        window.localStorage.removeItem("loggedInBlogappUser");
        blogService.setToken(null);
        setUser({ type: "user/reset" });
        navigate("/");
    };

    return (
        <AppBar
            sx={{
                position: "sticky",
                width: "100vw",
                marginBottom: "10px",
            }}
        >
            <Toolbar sx={{ alignItems: "stretch", gap: "30px" }}>
                <Button
                    component={Link}
                    to="/"
                    color="inherit"
                    sx={{
                        textTransform: "capitalize",
                        fontSize: "inherit",
                    }}
                >
                    Blogs
                </Button>
                <Button
                    component={Link}
                    to="/users"
                    color="inherit"
                    sx={{
                        textTransform: "capitalize",
                        fontSize: "inherit",
                    }}
                >
                    users
                </Button>
                <Typography
                    sx={{
                        fontSize: "inherit",
                        display: "flex",
                        alignItems: "center",
                        marginLeft: "auto",
                        gap: "10px",
                    }}
                >
                    {user.name} logged in{" "}
                    <Button
                        color="secondary"
                        type="button"
                        onClick={logOut}
                        variant="contained"
                        disableElevation
                        endIcon={<LogoutIcon />}
                    >
                        logout
                    </Button>
                </Typography>
            </Toolbar>
        </AppBar>
    );
};

export default NavigationPane;
